.page-header {
  background-color: #1B2D37;
  display: flex; }

.page-header__wrap {
  display: flex;
  align-items: flex-start;
  margin: 33px auto 0 auto;
  padding: 0 25px 20px 25px;
  width: 1462px;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .page-header__wrap {
      margin-top: 21px; } }
  @media (max-width: 767px) {
    .page-header__wrap {
      padding: 0 17px 0 17px;
      margin-top: 17px; } }

.page-header__logo {
  flex-grow: 1; }

.page-header__nav {
  display: flex;
  align-items: center;
  margin-right: 2px; }
  @media (max-width: 1023px) {
    .page-header__nav {
      margin-top: 11px; } }
  @media (max-width: 767px) {
    .page-header__nav {
      margin-top: 6px; } }

.page-header__call {
  margin-left: 49px;
  margin-top: 4px; }
  @media (max-width: 1023px) {
    .page-header__call {
      display: none; } }

.page-header__tel {
  position: relative;
  line-height: 18px;
  margin-left: 87px;
  padding-left: 34px; }
  @media (max-width: 1023px) {
    .page-header__tel {
      padding-left: 22px;
      margin-left: 19px; } }
  @media (max-width: 767px) {
    .page-header__tel {
      padding-left: 20px;
      margin-left: 9px; } }
  .page-header__tel::before {
    position: absolute;
    content: "";
    background-image: url("../../img/phone-call.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 18px;
    height: 18px;
    left: 0;
    top: 0; }
    @media (max-width: 1023px) {
      .page-header__tel::before {
        width: 24px;
        height: 24px; } }

.page-header__mail {
  position: relative;
  line-height: 18px;
  padding-left: 40px; }
  .page-header__mail::before {
    position: absolute;
    content: "";
    background-image: url("../../img/envelope.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 19px;
    height: 14px;
    left: 0;
    top: 2px; }
    @media (max-width: 1023px) {
      .page-header__mail::before {
        width: 25px;
        height: 19px; } }

@media (max-width: 1023px) {
  .page-header__mail a {
    display: none; } }

@media (max-width: 1023px) {
  .page-header__tel a {
    display: none; } }

.logo {
  display: flex; }

.logo img {
  width: 52px;
  height: 52px;
  margin-right: 16px; }
  @media (max-width: 767px) {
    .logo img {
      width: 38px;
      height: 38px; } }

.logo__text {
  margin-top: 5px; }

.logo__text-big {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 29px; }

.logo__text-small {
  margin: 0;
  color: #788891;
  font-family: "Roboto ", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px; }

.button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.05em;
  border: 1px solid #ffffff;
  border-radius: 5px;
  padding: 14px 27px 13px 26px;
  text-transform: uppercase; }
